.card-wraper_link {
  cursor: pointer;
  z-index: 0;
  border-radius: 10px;
  outline: none;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.card-wraper_link:before {
  content: "";
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  opacity: 0;
  background: linear-gradient(45deg, red, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, red) 0 0 / 400%;
  border-radius: 10px;
  transition: opacity .3s ease-in-out;
  animation: glowing 20s linear infinite;
  position: absolute;
  top: -2px;
  left: -2px;
}

@media screen and (min-width: 1200px) {
  .card-wraper_link:before {
    width: calc(100% + 13px);
    height: calc(100% + 13px);
  }
}

.card-wraper_link:active {
  color: #000;
}

.card-wraper_link:active:after {
  background: none;
}

.card-wraper_link:hover:before, .card-wraper_link:focus:before {
  opacity: 1;
}

.card-wraper_link:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background: #111;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

.card-wraper_link:hover .card-img, .card-wraper_link:focus .card-img {
  filter: saturate(165%);
  transform: scale(.97);
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

